.js .heading-block__heading, .js .heading-block__subheading, .js .reviews-block-text__heading, .js .reviews-block-text__subheading {
  opacity: 0;
  transform: translateX(var(--slide-x, 0));
  transition: transform 1.5s cubic-bezier(0.2, 0.7, 0.2, 1), opacity 1s ease;
  will-change: transform, opacity;
}
.js .heading-block__heading, .js .reviews-block-text__heading {
  --slide-x: -6rem;
}
.js .heading-block__subheading, .js .reviews-block-text__subheading {
  --slide-x: 6rem;
}

.js .heading-block.is-in-view .heading-block__heading,
.js .heading-block.is-in-view .heading-block__subheading,
.js .reviews-block-text.is-in-view .reviews-block-text__heading,
.js .reviews-block-text.is-in-view .reviews-block-text__subheading {
  opacity: 1;
  transform: none;
}

@media (prefers-reduced-motion: reduce) {
  .heading-block__heading, .heading-block__subheading, .reviews-block-text__heading, .reviews-block-text__subheading {
    transition: none;
    opacity: 1;
    transform: none;
  }
}
.vertical-slide-yes {
  opacity: 0 !important;
  transform: translateY(40px) scale(1);
  transition: transform 1s ease, opacity 1s ease;
}

.vertical-slide-yes.is-visible {
  opacity: 1 !important;
  transform: translateY(0) scale(1);
}

.horizontal-slide-yes {
  transition: all 1.2s cubic-bezier(0.03, 0.6, 0.33, 0.96);
}
.horizontal-slide-yes.from-left {
  margin-left: -100%;
}
.horizontal-slide-yes.from-right {
  margin-right: -100%;
}

.horizontal-slide-yes.is-visible.from-left {
  margin-left: 0;
}
.horizontal-slide-yes.is-visible.from-right {
  margin-right: 0;
}

.slow-column-scroll,
.fast-column-scroll {
  transition: transform 0.2s ease-out;
  will-change: transform;
}

.category-header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  isolation: isolate;
  /* banner sizing */
  min-height: clamp(320px, 46vw, 800px);
  margin-bottom: clamp(24px, 5vw, 64px);
  /* one mask to rule them all */
  --fade-start: 60%;
  --fade-end: 100%;
  -webkit-mask-image: linear-gradient(to bottom, #000 0%, #000 var(--fade-start), rgba(0, 0, 0, 0) var(--fade-end));
  mask-image: linear-gradient(to bottom, #000 0%, #000 var(--fade-start), rgba(0, 0, 0, 0) var(--fade-end));
  -webkit-mask-size: 100% 100%;
  mask-size: 100% 100%;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  /* overlay sits above the image, below the text */
}
.category-header::before {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.45); /* tune darkness */
  z-index: 1;
  pointer-events: none;
}
.category-header__image {
  position: absolute;
  inset: 0;
  z-index: 0; /* below overlay */
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* remove the mask from the image itself */
  -webkit-mask: none;
  mask: none;
}
.category-header__title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  z-index: 2;
  height: 100%;
  text-align: center;
}
.category-header__heading {
  width: 100%;
  font-size: clamp(32px, 55vw, 10vw);
  color: #ffffff;
  white-space: nowrap;
}
.category-header__subheading {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 4px;
  color: #be0002;
  font-size: clamp(24px, 55vw, 6vw);
  font-family: "LibreBaskerville-Regular", serif;
  white-space: nowrap;
}
