/* =========================================
   Primary Navigation (drawer on mobile)
========================================= */
#site-navigation {
  /* Mobile drawer defaults */
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0; /* default: full height */
  transform: translateX(100%);
  transition: transform 0.2s linear;
  background-color: #131D38;
  z-index: 90; /* below logo and buttons */
  padding: 16px 0 24px;
  overflow-y: scroll;
}
@media (min-width: 80em) {
  #site-navigation {
    position: relative;
    inset: auto;
    transform: none;
    background: transparent;
    z-index: auto;
    padding: 0;
    overflow-y: unset;
  }
}

.menu-primary-menu-container {
  padding-top: 72px;
}
@media (min-width: 80em) {
  .menu-primary-menu-container {
    padding-top: unset;
  }
}

/* Open state for mobile */
.active #site-navigation {
  transform: translateX(0);
}

/* Top level ul */
#site-navigation .menu {
  margin: 0;
  padding: 0 8px;
  list-style: none;
  /* vertical list on mobile */
}
@media (max-width: 79.98em) {
  #site-navigation .menu li {
    margin: 0;
  }
}
@media (min-width: 80em) {
  #site-navigation .menu {
    display: flex;
    align-items: center;
    gap: 16px;
  }
}

/* Top level li */
#site-navigation .menu > li {
  position: relative;
}

/* Top level links */
#site-navigation .menu > li > a {
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 18px 18px;
  text-decoration: none;
  transition: color 0.18s ease;
}

#site-navigation .menu > li:hover > a,
#site-navigation .menu > li.current-menu-item > a,
#site-navigation .menu > li.current-menu-ancestor > a {
  color: #ffffff;
}

/* =========================================
   Desktop dropdowns
========================================= */
#site-navigation .menu > li > .sub-menu {
  position: absolute;
  top: calc(100% - 2px);
  left: 8px;
  min-width: 400px;
  margin: 0;
  padding: 14px;
  list-style: none;
  background: #131D38;
  border-radius: 12px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(6px);
  transition: opacity 0.18s ease, transform 0.18s ease, visibility 0s linear 0.18s;
}

#site-navigation .menu > li:hover > .sub-menu,
#site-navigation .menu > li:focus-within > .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  transition-delay: 0s;
}

/* Nested submenus (fly right on desktop) */
#site-navigation .sub-menu .sub-menu {
  top: -8px;
  left: calc(100% + 8px);
}

/* Submenu items base */
#site-navigation .sub-menu li {
  position: relative;
}

#site-navigation .sub-menu a {
  display: block;
  padding: 10px 12px;
  text-decoration: none;
  border-radius: 8px;
  transition: background-color 0.18s ease, color 0.18s ease;
}

/* =========================================
   Mobile accordion (overrides)
   Requires JS to toggle: li.menu-item-has-children.is-open
========================================= */
@media (max-width: 79.98em) {
  /* Parent links get a caret and extra tap area */
  #site-navigation .menu li.menu-item-has-children > a {
    position: relative;
    padding-right: 44px;
  }
  #site-navigation .menu li.menu-item-has-children > a::after {
    content: "\f107"; /* FontAwesome chevron-down */
    font-family: fontawesome;
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translateY(-50%);
    transition: transform 0.2s ease;
  }
  #site-navigation .menu li.is-open > a::after {
    transform: translateY(-50%) rotate(180deg);
  }
  /* Submenus stack, hidden by default, slide open */
  #site-navigation .menu li > .sub-menu {
    position: static;
    overflow: hidden;
    max-height: 0;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-4px);
    margin: 0;
    padding: 0;
    border-radius: 0;
    transition: max-height 0.25s ease, opacity 0.2s ease, transform 0.2s ease, visibility 0s linear 0.25s; /* delay visibility toggle to end of slide */
  }
  #site-navigation .menu li.is-open > .sub-menu {
    max-height: 1000px; /* sufficiently large for long lists */
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    transition: max-height 0.3s ease, opacity 0.2s ease, transform 0.2s ease;
  }
  /* Mobile submenu link spacing */
  #site-navigation .sub-menu a {
    display: block;
    padding: 12px 18px;
    border-radius: 0;
  }
  /* Neutralize desktop hover behavior on mobile */
  #site-navigation .menu > li:hover > .sub-menu,
  #site-navigation .menu > li:focus-within > .sub-menu {
    opacity: 1; /* keep focus styles sane */
    transform: none;
  }
}
/* =========================================
   A11y + motion preferences
========================================= */
#site-navigation a:focus-visible {
  outline: 2px solid #4cc3ff;
  outline-offset: 2px;
  border-radius: 10px;
}

@media (prefers-reduced-motion: reduce) {
  #site-navigation * {
    transition: none !important;
    animation: none !important;
  }
}
