    height: 100%;
    /* fade the RIGHT edge into white */
    --edge-fade: 28%;
    -webkit-mask-image: linear-gradient(to right, #000 0%, #000 calc(100% - var(--edge-fade)), rgba(0, 0, 0, 0) 100%);
    mask-image: linear-gradient(to right, #000 0%, #000 calc(100% - var(--edge-fade)), rgba(0, 0, 0, 0) 100%);
