}

/* ===== Animation (JS toggles .is-in-view on the inner block) ===== */
.js .animated-two-column-block {
  /* Stagger: heading first, then description */
}
.js .animated-two-column-block__heading, .js .animated-two-column-block__description {
  opacity: 0;
  transform: translateX(var(--slide-x, 4rem)); /* direction set per block */
  transition: transform 820ms cubic-bezier(0.2, 0.7, 0.2, 1), opacity 800ms ease;
  will-change: transform, opacity;
}
.js .animated-two-column-block.is-in-view .animated-two-column-block__heading {
  opacity: 1;
  transform: translateX(0);
  transition-delay: 400ms;
}
.js .animated-two-column-block.is-in-view .animated-two-column-block__description {
  opacity: 1;
  transform: translateX(0);
  transition-delay: 700ms;
}

